.animal-card {
  background: #ffffff;
  border-radius: 16px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.25s ease, box-shadow 0.25s ease;
  display: flex;
  flex-direction: column;
  max-width: 300px;
}

.animal-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
}

.image-container {
  width: 100%;
  height: 200px;
  overflow: hidden;
  background: #f5f5f5;
}

.image-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.animal-info {
  padding: 16px;
}

.animal-info h3 {
  font-size: 1.4rem;
  margin: 0 0 4px;
  color: #333;
}

.animal-info .scientific {
  font-style: italic;
  font-size: 0.95rem;
  color: #666;
  margin-bottom: 8px;
}

.animal-info .details {
  font-size: 0.9rem;
  color: #444;
}

.animal-info .fun-fact {
  font-size: 0.85rem;
  color: #2b7a78;
  margin-top: 10px;
  background: #def2f1;
  border-radius: 8px;
  padding: 6px 10px;
}
