/* src/App.css */
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  background: linear-gradient(135deg, #e0f7fa, #f1f8ff);
  color: #222;
  min-height: 100vh;
}

.App {
  text-align: center;
  padding-bottom: 60px;
}

h1 {
  font-size: 2.5rem;
  color: #003049;
  margin: 40px 0 25px;
  letter-spacing: 1px;
}

.animal-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 30px;
  padding: 0 40px 60px;
  justify-items: center;
}

.no-results {
  font-size: 1.2rem;
  color: #555;
  margin-top: 40px;
}

.about-page {
  max-width: 900px;
  margin: 60px auto;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 20px;
  padding: 40px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(15px);
  line-height: 1.8;
  animation: fadeIn 0.5s ease;
}

.about-page h1 {
  font-size: 2rem;
  color: #002c54;
  margin-bottom: 15px;
}

.about-page p {
  color: #333;
  font-size: 1.1rem;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

@media (max-width: 600px) {
  .animal-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    padding: 0 20px;
  }
}
.toggle-info-btn {
  background: linear-gradient(90deg, #2e8b57, #3cb371);
  border: none;
  border-radius: 25px;
  color: white;
  padding: 8px 16px;
  cursor: pointer;
  font-weight: 500;
  transition: 0.3s;
  margin-top: 8px;
}

.toggle-info-btn:hover {
  background: linear-gradient(90deg, #3cb371, #2e8b57);
  transform: scale(1.05);
}

.scientific-info {
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  padding: 10px;
  margin-top: 10px;
  animation: fadeIn 0.3s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-5px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
