.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.65);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(5px);
}

.modal-content {
  background: #ffffff;
  border-radius: 20px;
  max-width: 900px;
  width: 90%;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
  position: relative;
  animation: fadeIn 0.3s ease;
}

.close-btn {
  background: #2b7a78;
  color: #fff;
  border: none;
  padding: 10px 18px;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  margin: 16px;
  transition: background 0.2s;
}
.close-btn:hover {
  background: #3aafa9;
}

.modal-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.modal-image {
  width: 100%;
  max-height: 400px;
  object-fit: cover;
  border-radius: 12px;
}

.modal-info {
  width: 100%;
  margin-top: 20px;
  text-align: left;
}

.modal-info h2 {
  margin-bottom: 8px;
}

.modal-info .scientific {
  font-style: italic;
  color: #555;
  margin-bottom: 12px;
}

.modal-info ul {
  list-style: none;
  padding: 0;
  margin-bottom: 20px;
}
.modal-info ul li {
  margin: 4px 0;
}

.fun-fact {
  background: #def2f1;
  border-left: 5px solid #2b7a78;
  padding: 10px 15px;
  border-radius: 6px;
  color: #17252a;
  margin-top: 16px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
