/* RegionTabs.css */
.region-tabs {
  display:flex;
  gap:12px;
  justify-content:center;
  flex-wrap:wrap;
  padding: 10px 20px;
}

/* base tab look */
.region-tab {
  padding:10px 18px;
  border-radius:999px;
  border: none;
  color: #fff;
  cursor:pointer;
  font-weight:700;
  font-size:0.95rem;
  transition: transform .2s ease, box-shadow .2s ease;
  box-shadow: 0 6px 18px rgba(0,0,0,0.08);
}

/* different color groups via data attribute or nth-child (you can keep nth-child) */
.region-tab:nth-child(1) { background: linear-gradient(90deg,#ff7e5f,#feb47b); } /* All */
.region-tab:nth-child(2) { background: linear-gradient(90deg,#ff9a9e,#fecfef); } /* Africa */
.region-tab:nth-child(3) { background: linear-gradient(90deg,#7bc6cc,#be93c5); } /* Asia */
.region-tab:nth-child(4) { background: linear-gradient(90deg,#89f7fe,#66a6ff); } /* N. America */
.region-tab:nth-child(5) { background: linear-gradient(90deg,#f6d365,#fda085); } /* Australia */
.region-tab:nth-child(6) { background: linear-gradient(90deg,#cfd9df,#e2ebf0); color:#222; } /* Antarctica */
.region-tab:nth-child(7) { background: linear-gradient(90deg,#43cea2,#185a9d); } /* Oceans */
.region-tab:nth-child(8) { background: linear-gradient(90deg,#fbc2eb,#a6c1ee); } /* Europe */
.region-tab:nth-child(9) { background: linear-gradient(90deg,#f7797d,#FBD786); } /* S. America */

/* active style (high specificity) */
.region-tab.active,
.region-tab[aria-pressed="true"] {
  transform: translateY(-6px) scale(1.03);
  box-shadow: 0 14px 40px rgba(0,0,0,0.24);
  outline: 3px solid rgba(255,255,255,0.25);
  filter: saturate(1.08);
}
