/* NavBar.css (replace or merge with your existing file) */
.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 18px;
  padding: 12px 24px;
  background: linear-gradient(90deg,#0575e6,#00f2fe); /* vivid gradient */
  color: white;
  position: sticky;
  top: 0;
  z-index: 2000;
  box-shadow: 0 6px 18px rgba(0,0,0,0.12);
}

.brand {
  margin: 0;
  cursor: pointer;
  font-weight: 700;
  font-size: 1.25rem;
  letter-spacing: 0.5px;
  color: white;
}

.nav-center {
  display:flex;
  gap:12px;
  align-items:center;
}

/* Stronger specificity for nav-item and its active state */
.nav-item {
  border: none;
  background: linear-gradient(135deg, rgba(255,255,255,0.15), rgba(255,255,255,0.08));
  color: white;
  padding: 8px 16px;
  border-radius: 999px;
  cursor: pointer;
  font-weight: 600;
  transition: transform .18s ease, box-shadow .18s ease;
  box-shadow: 0 4px 10px rgba(0,0,0,0.08);
}

/* make active state unmistakable */
.nav-item.active,
.nav-item:focus,
.nav-item[aria-pressed="true"] {
  background: linear-gradient(90deg,#ffd86b,#ff7a7a);
  color: #1b1b1b;
  transform: translateY(-3px);
  box-shadow: 0 10px 30px rgba(255,123,123,0.18);
}

/* Search bar */
.search-bar {
  display:flex;
  align-items:center;
  gap:10px;
  background: rgba(255,255,255,0.95);
  padding: 6px 10px;
  border-radius: 999px;
  min-width: 220px;
  box-shadow: 0 6px 18px rgba(0,0,0,0.08);
  border: 1px solid rgba(0,0,0,0.06);
}

.search-bar input {
  border: none;
  outline: none;
  background: transparent;
  font-size: 0.95rem;
  width: 160px;
  color: #003049;
}

/* responsive: collapse search width */
@media (max-width:720px) {
  .search-bar { min-width:140px; }
  .search-bar input { width: 110px; }
}
